import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ArticlesService } from 'src/app/common/modules/articles/articles.service';
import { ActivatedRouteSnapshot, ActivatedRoute } from '@angular/router';

@Component({
    selector: 'grid-article',
	templateUrl: './article.template.html',
})
export class gridArticleComponent implements OnInit {

    static minGridColSize = 7;
    static maxGridColSize = 9;
    static minGridRowSize = 9;
    static maxGridRowSize = 15;

	constructor(
        private articleService: ArticlesService,
        private route: ActivatedRoute
    ){}

    @Input() options;
    loaded;
    id;
    article;

    ngOnInit(){
        this.loaded = false;
        const options = JSON.parse(this.options);
        this.route.paramMap.subscribe(params => { this.id = params.get("articleId") });
        console.log(this.id);
        this.articleService.getArticle(this.id).then(article => {
            this.article = article;
            this.loaded = true;
        });
        // }
        // if(options.podle){
        //     this.podle = options.podle;
        // }
        // if(options.order){
        //     this.order = options.order;
        // }
    }

}
