import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ArticlesService } from 'src/app/common/modules/articles/articles.service';
import { CategoriesService } from 'src/app/common/modules/categories/categories.service';

@Component({
    selector: 'grid-article-preview',
    templateUrl: './article.template.html',
    styleUrls: [
        './article.styles.scss'
    ]
})
export class gridArticlePreviewComponent implements OnInit {

    static minGridColSize = 1;
    static maxGridColSize = 4;
    static minGridRowSize = 2;
    static maxGridRowSize = 6;

	constructor(private articleService: ArticlesService, private categoriesService: CategoriesService){}

    @Input() options;
    @Input() editmode;
    loaded;
    article;
    category;
    exact;
    index;
    order;
    podle;

    ngOnInit(){
        this.loaded = false;
        const options = JSON.parse(this.options);
        if(options.exact && !options.index){
            this.exact = options.exact;
            this.articleService.getArticle(this.exact).then(article => {
                this.article = article;
                this.loaded = true;
            })
        }
        if(options.podle){
            this.podle = options.podle;
        }
        if(options.index){
            this.index = options.index;
            console.log(this.index.toString());
            this.articleService.getByNth('desc',this.index - 1,'1').then(article => {
                this.article = article[0];
                if(article[0].categories){
                    this.categoriesService.getCategory(article[0].categories[0]).then(category => {
                        console.log(category);
                        this.category = category;
                    })
                }
                this.loaded = true;
            })
        }
        if(options.order){
            this.order = options.order;
        }
    }


    openArticle() {

    }

}
