import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'grid-text',
	templateUrl: './text.template.html',
})
export class gridTextComponent implements OnInit {

    static minGridColSize = 2;
    static maxGridColSize = 50;
    static minGridRowSize = 1;
    static maxGridRowSize = 50;

	constructor(){}

    @Input() id;
    @Input() options;
    @Output() hasClasses = new EventEmitter<string>();
    text;
    class;

    ngOnInit(){
        const options = JSON.parse(this.options);
        this.text = options.text;
        if(options.class){
            this.class= options.class;
            this.hasClasses.emit("parent-" + this.class + "|" + this.id);
        }
    }

}
