import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PagesService } from 'src/app/common/modules/pages/pages.service';
import { Router } from '@angular/router';

@Component({
    selector: 'grid-menu',
    templateUrl: './menu.template.html',
    styleUrls: [
        './menu.styles.scss'
    ]
})
export class gridMenuComponent implements OnInit {

    static minGridColSize = 7;
    static maxGridColSize = 20;
    static minGridRowSize = 1;
    static maxGridRowSize = 1;

	constructor(
        private pageService:PagesService,
        private router: Router
    ){}

    @Input() options;
    links;
    orientaion;

    ngOnInit(){
        const options = JSON.parse(this.options);
        this.pageService.getPages().then(data => {
            var links = [];
            var index;
            data.forEach(element => {
                if(element[0] !=  'index' && element[0] !=  'categories' && element[0] != 'articles' ){
                    links.push(element);
                }else if(element[0] == 'index'){
                    index = element;
                }
            });
            //links.reverse();
            //[this.links[links.length], this.links[1]] = [this.links[1],this.links[links.length]];
            this.links = links;
            console.log(links);
            this.links.push(index);
        })
        //this.imgUrl = options.imageUrl;
    }

    goto(link){
        console.log("ok");
        this.router.navigateByUrl('/', {skipLocationChange: true}).then(()=>{
            this.router.navigated = false;
            this.router.navigate([link])
        });
    }

}
