import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ArticlesService } from 'src/app/common/modules/articles/articles.service';
import { ActivatedRouteSnapshot, ActivatedRoute } from '@angular/router';

@Component({
    selector: 'grid-archive',
	templateUrl: './archive.template.html',
})
export class gridArchiveComponent implements OnInit {

    static minGridColSize = 2;
    static maxGridColSize = 50;
    static minGridRowSize = 2;
    static maxGridRowSize = 50;

	constructor(
        private articleService: ArticlesService,
        private route: ActivatedRoute
    ){}

    @Input() options;
    loaded;
    articles;

    ngOnInit(){
        this.loaded = false;
        const options = JSON.parse(this.options);
        this.articleService.getArticles().then(articles => {
            //this.articles = articles;
            var arts = [];
            articles.forEach(article => {
                this.articleService.getArticle(article).then(art => {
                    arts.push(art);
                });
            });
            this.articles = arts;
            this.loaded = true;
        });
    }

}
