import { RouterModule, Routes } from '@angular/router';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
/* Third party*/
import { GridsterModule } from 'angular-gridster2';
/* Internal */
import { LandingComponent } from './landing.component';
import { SharedMoudle } from 'src/app/shared.module';
import { TranslateModule } from '@ngx-translate/core';
import { DynamicModule } from 'ng-dynamic-component';
import { core_routes } from '../routes';
import { gridTextComponent } from 'src/app/common/components/grid-components/text/text.component';
import { PageComponent } from '../../components/page/page.component';
import { PageViewService } from '../../components/page/page.view.service';
import { FormsModule } from '@angular/forms';
import { QuillModule } from 'ngx-quill';
import { gridImageComponent } from 'src/app/common/components/grid-components/image/image.component';
import { ArticlesService } from 'src/app/common/modules/articles/articles.service';
import { gridArticlePreviewComponent } from 'src/app/common/components/grid-components/articlePreview/article.component';
import { gridArticleComponent } from 'src/app/common/components/grid-components/article/article.component';
import { gridMenuComponent } from 'src/app/common/components/grid-components/menu/menu.component';
import { gridAdComponent } from 'src/app/common/components/grid-components/ad/ad.component';
import { PagesService } from 'src/app/common/modules/pages/pages.service';
import { ToastsModule } from '../../dependencies/toast/toast.module';
import { ToastrModule, ToastNoAnimationModule } from 'ngx-toastr';
import { CategoriesService } from 'src/app/common/modules/categories/categories.service';
import { gridCategoriesComponent } from 'src/app/common/components/grid-components/categories/categories.component';
import { gridArchiveComponent } from 'src/app/common/components/grid-components/archive/archive.component';

/**
 * @author Schwada
 * @since 9.1.2018
 *
 * This is a wrapper/template for landing pages e.g. login,unreachable,etc..
 * and should only be accessed by unauthenicated users.
 * It also collects all the landing page routes
 *
 */


export const landing_routes: Routes = [
	{
    path: '',
		children:[ ...core_routes ]
  },
  {
    path: 'page/:pageid',
    component: PageComponent
  },
  {
    path: 'index',
    component: PageComponent
  },
  {
    path: 'article/:articleId',
    component: PageComponent
  }
]

@NgModule({
    declarations: [
        LandingComponent,
        PageComponent,
        gridAdComponent,
        gridMenuComponent,
        gridTextComponent,
        gridImageComponent,
        gridArticleComponent,
        gridCategoriesComponent,
        gridArticlePreviewComponent,
        gridArchiveComponent
    ],
    imports: [
        ToastrModule,
        CommonModule,
        GridsterModule,
        SharedMoudle,
        FormsModule,
        QuillModule,
        TranslateModule,
        DynamicModule.withComponents([
            gridTextComponent,
            gridImageComponent,
            gridArticlePreviewComponent,
            gridArticleComponent,
            gridMenuComponent,
            gridAdComponent,
            gridCategoriesComponent,
            gridArchiveComponent
        ]),
        RouterModule.forChild(landing_routes)
    ],
    providers: [
        [{ provide: CategoriesService, useClass: CategoriesService}],
        [{ provide: PageViewService, useClass: PageViewService}],
        [{ provide: ArticlesService, useClass: ArticlesService}],
        [{ provide: PagesService, useClass: PagesService}]
    ],
    exports: [ RouterModule ]
})
export class LandingModule { }
