

import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';
import { GridsterConfig, GridsterItem, DisplayGrid, GridType }  from 'angular-gridster2';
import { PageViewService } from './page.view.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AuthenticationService } from '../../services';
import { CookiesService } from '../../services/cookies/cookies.service';
import { Authinfo } from 'src/app/shared/models/Authinfo';
import { element } from '@angular/core/src/render3';
import { gridTextComponent } from 'src/app/common/components/grid-components/text/text.component';
import { ToastrService } from 'ngx-toastr';

@Component({
  templateUrl: './page.template.html',
  styleUrls: ['./page.styles.scss']
})
export class PageComponent implements OnInit {
    //could use some cleenup


    private page: string;
    private pageType;
    private gridId: string;
    private articleCount = 0; //realtes to ordered articles
    private editmode: boolean;
    private loaded: boolean;

    private removedItems = [];

    //create new grid item
    private componentDefinitions = [];
    private chosenComponent;
    private chosenComponentOptions = {};

    constructor(
      private authentication: AuthenticationService,
      private pageService: PageViewService,
      private modalService: NgbModal,
      private route: ActivatedRoute,
      public toast: ToastrService,
      private router: Router
    ) { }

    ngOnInit() {
      this.loaded = false;
      //an interceptor in case the page dosent load should try to load it again atleast 2 more times

      this.editmode = false;

      if(this.authentication._authenticated == true){
        this.editmode = true;
      };

      if(this.route.snapshot.url[0].path == 'index'){
        this.page = "index";
        this.pageType = "index";
      }else if(this.route.snapshot.url[0].path == 'article'){
        this.page = 'articles';
        this.pageType = "article"
      }else{
        this.pageType = ""
        this.route.paramMap.subscribe(params => { this.page = params.get("pageid") });
      }

      this.options = {
          gridType: GridType.VerticalFixed,
          displayGrid: DisplayGrid.OnDragAndResize,
          itemResizeCallback: PageComponent.itemResize,
          //gridSizeChangedCallback: PageComponent.gridResize,
          //itemResizeCallback: PageComponent.itemResize,
          //itemInitCallback: PageComponent.itemInit,
          //itemRemovedCallback: PageComponent.itemRemoved,)
          disableWindowResize:false,
          fixedColWidth: 50,
          fixedRowHeight: 50,
          minCols:12,
          minRows:12,
          maxCols:64,
          maxRows:64,
          isMobile: false,
          mobileBreakpoint: 0,
          pushItems: this.editmode,
          draggable: {
            enabled: this.editmode
          },
          resizable: {
            enabled: this.editmode,
            //stop: this.updateGrid
          }
        }

      this.pageService.getPageGrid(this.page).then((data) => {
        this.gridId = data[0].grid;
        this.dashboard = data;
        console.log(data)
        this.loaded = true;
        this.calibrateOrderedArticles();
      });

  }

    options: GridsterConfig;
    dashboard: Array<GridsterItem>;

    getComponent(component) {
        return PageViewService.getStringToComponentDefinitions(component);
    }

    // static gridResize(gridsterComponent){
    //     //fix recurison problem
    //     gridsterComponent.grid.forEach(element => {
    //         PageComponent.itemResize(element.item,element);
    //     });
    // }

    static itemResize(item, itemComponent) {
        let component = PageViewService.getStringToComponentDefinitions(item.component);
        let columns = itemComponent.gridster.columns;
        //let rows = itemComponent
        //probably create a better way

        let minCol = Math.round(columns * (component.minGridColSize / 12));
        if(itemComponent.$item.cols < minCol ){
            itemComponent.$item.cols = minCol;
            itemComponent.setSize();
            //itemComponent.checkItemChanges(itemComponent.$item, itemComponent.item);
            //this.toast.error("takhle mal pole bt neme");
        }
        let maxCol = Math.round(columns * (component.maxGridColSize / 12));
        if(itemComponent.$item.cols > maxCol){
            itemComponent.$item.cols = maxCol;
            itemComponent.setSize();
            //itemComponent.checkItemChanges(itemComponent.$item, itemComponent.item);
            //this.toast.error("takhle velk pole bt neme");
        }
        if(itemComponent.$item.rows < component.minGridRowSize){
            itemComponent.$item.rows = component.minGridRowSize;
            itemComponent.setSize();
            itemComponent.checkItemChanges(itemComponent.$item, itemComponent.item);
            //this.toast.error("takhle mal pole bt neme");
        }
        if(itemComponent.$item.rows > component.maxGridRowSize){
            itemComponent.$item.rows = component.maxGridRowSize;
            itemComponent.setSize();
            itemComponent.checkItemChanges(itemComponent.$item, itemComponent.item);
            //this.toast.error("takhle velk pole bt neme");
        }
    }

    changedOptions() {
      this.options.api.optionsChanged();
    }

    removeItem(event,item) {
      this.removedItems.push(item.id);
      this.dashboard.splice(this.dashboard.indexOf(item), 1);
      this.calibrateOrderedArticles();
    }

    addItem(data) {
        this.dashboard.push(data);
    }

    saveGrid(){
      this.loaded = false;
      this.dashboard.forEach(element => {
          console.log(this.page,element);
        this.pageService.savePageGrid(this.page,element).then((data) => {
          this.loaded = true;
        })
      });

      this.removedItems.forEach(element => {
        this.pageService.deletePageGrid(element).then(data => {
        })
      })

    }

    calibrateOrderedArticles(){
        //this is pretty crap find something better
        this.articleCount = 0
        var orderedArticlesIndexes = {};
        this.dashboard.forEach(element => {
            if(element.component == 'gridArticlePreviewComponent' ){
                let options = JSON.parse(element.options);
                if(options['podle'] == 'order'){
                    this.articleCount++
                    orderedArticlesIndexes[options['index']] = this.dashboard.indexOf(element);
                }
            }
        });
        let keys = Object.keys(orderedArticlesIndexes);
        keys.sort();

        for (let i = 0; i < keys.length; i++) {
            let k = keys[i];
            let indexOfArticle = parseInt(orderedArticlesIndexes[k]);
            let options = JSON.parse(this.dashboard[indexOfArticle].options);
            options.index = i +1;
            this.dashboard[indexOfArticle].options = JSON.stringify(options);
        }
    }

    outputs = {
        hasClasses: (clazz) => {
            setTimeout(()=> {
            var clazId = clazz.split("|");
            this.dashboard.forEach(element => {
                //console.log(clazId[1], element.id);
                if(element.id == clazId[1]){
                    let index = this.dashboard.indexOf(element);
                    this.dashboard[index]['class'] = clazId[0];
                    //element['clazz'] =
                }
            });
            }, 0);
        }

    };

    editItem(event,item,content){
        console.log(this.page);
        this.componentDefinitions = this.pageService.getComponentDefinitions();
        this.componentDefinitions.forEach(element => {
            if(element.name == item.component ){
                this.chosenComponent = element;
            }
        });

        let options = JSON.parse(item.options);
        this.chosenComponentOptions = options;

        this.modalService.open(content,{ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {

          this.pageService.savePageGrid(
              this.page,
              {id:item.id,grid:this.gridId ,cols: item.cols, rows: item.rows, y: item.y, x: item.x,options:JSON.stringify(this.chosenComponentOptions),component:item.component},
              ).then(data =>{
            //this.addItem( {cols: com.minGridColSize, rows: 1, y: com.minGridRowSize, x: 0,options:JSON.stringify(this.chosenComponentOptions),component:this.chosenComponent.name});
          });

        }, (reason) => {
          //this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
        });
    }

    open(content) {
      this.componentDefinitions = this.pageService.getComponentDefinitions();
      this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
        let com = this.getComponent(this.chosenComponent.name);

        if(this.chosenComponent.name == 'gridArticlePreviewComponent'){
            this.chosenComponentOptions['index'] = this.articleCount + 1;
        }
        this.pageService.createPageGrid(
            {grid:this.gridId ,cols: com.minGridColSize, rows: com.minGridRowSize, y: 0, x: 0,options:JSON.stringify(this.chosenComponentOptions),component:this.chosenComponent.name}
            ,this.page
        ).then(data =>{
          this.addItem( {cols: com.minGridColSize, rows: 1, y: com.minGridRowSize, x: 0,options:JSON.stringify(this.chosenComponentOptions),component:this.chosenComponent.name});
        });

      }, (reason) => {
        //this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });
    }





}
