import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CategoriesService } from 'src/app/common/modules/categories/categories.service';

@Component({
    selector: 'grid-categories',
    templateUrl: './categories.template.html'
})
export class gridCategoriesComponent implements OnInit {

    static minGridColSize = 3;
    static maxGridColSize = 4;
    static minGridRowSize = 6;
    static maxGridRowSize = 6;

	constructor(private categoriesService: CategoriesService){}

    @Input() options;
    categories = [];
    loaded;

    ngOnInit(){
        const options = JSON.parse(this.options);
        this.loaded = false;
        this.categoriesService.getCategories().then(data => {
            var categories = [];
            data.forEach(element => {
                console.log(element);
                this.categoriesService.getCategory(element).then(category =>{
                    categories.push(category);
                })
            });
            this.categories = categories;
        })
    }

}
