import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'grid-image',
	templateUrl: './image.template.html',
})
export class gridImageComponent implements OnInit {

    static minGridColSize = 1;
    static maxGridColSize = 50;
    static minGridRowSize = 1;
    static maxGridRowSize = 50;

	constructor(){}

    @Input() options;
    imgUrl;
    imgLink;

    ngOnInit(){
        const options = JSON.parse(this.options);
        this.imgUrl = options.imageUrl;
        if(options.imageLink){
            this.imgLink = options.imageLink;
        }
    }

}
